.blogPage {
   padding-top: 20px;
   width: 50vw;
   margin: 0 auto;
   display: flex;
   flex-direction: column;
   align-items: center;
}

.containSelectors {
   display: flex;
   width: 100%;
   justify-content: space-between;
   padding: 10px;
}

.sortKeySelector {
   background-color: #1B3A20;
   color: white;
   border: none;
   border-radius: 4px;
   padding: 2px 4px;
   cursor: pointer;
}

.pageSlider {
   display: flex;
   align-items: center;
   gap: 100px;
   font-size: 32px;
   margin-bottom: 10px;
}

@media(max-width: 1200px) {
   .blogPage {
      width: 70vw;
   }
}

@media(max-width: 800px) {
   .blogPage {
      width: 80vw;
   }
}

@media(max-width: 600px) {
   .blogPage {
      width: 90vw;
   }
}